<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <label class="form__label" :for="nameInput">
    <input class="form__input" :type="type" :name="nameInput" :id="nameInput" :placeholder="placeholderCenter"
      @input="check($event)" :ref="'ref_' + nameInput">
    <span class="form__value">{{ placeholder }}</span>
    <span class="form__error" v-if="error">{{ error }}</span>
  </label>
</template>
<script setup>
import { toRefs, ref, nextTick, onUpdated, onMounted } from 'vue'
const props = defineProps({
  placeholder: {
    type: String,
    required: true
  },
  placeholderCenter: {
    type: String,
  },
  error: {
    type: String,
  },
  nameInput: {
    type: String
  },
  type: {
    type: String
  },
  err: {
    type: Object
  }
})
const emit = defineEmits(['update'])
const { placeholder, placeholderCenter, error, nameInput, type, err } = toRefs(props);


const check = (params) => {
  emit('update', params.target.value);
}

const ref_name = ref(null)
const ref_address = ref(null)
const ref_phone = ref(null)
const ref_email = ref(null)



onUpdated(() => {
  nextTick(() => {
    if (typeof (error.value) != 'undefined') {
      if (ref_name.value!==null && err.value['name'] !== undefined) {
        window.scrollBy({
          top: ref_name.value.getBoundingClientRect().top,
          behavior: 'smooth'
        });
        ref_name.value.focus()
      } else if (ref_address.value!==null && err.value['address'] !== undefined) {
        window.scrollBy({
          top: ref_address.value.getBoundingClientRect().top,
          behavior: 'smooth'
        });
        ref_address.value.focus()
      } else if (ref_phone.value!==null && err.value['phone'] !== undefined) {
        window.scrollBy({
          top: ref_phone.value.getBoundingClientRect().top,
          behavior: 'smooth'
        });
        ref_phone.value.focus()
      } else if (ref_email.value!==null && err.value['email'] !== undefined) {
        window.scrollBy({
          top:ref_email.value.getBoundingClientRect().top,
          behavior: 'smooth'
        });
        ref_email.value.focus()
      }
    }
  })
})
</script>
